	module.exports = {"pathContext":{"docHtml":"<h1>camelcase <a href=\"https://travis-ci.org/sindresorhus/camelcase\"><img src=\"https://travis-ci.org/sindresorhus/camelcase.svg?branch=master\" alt=\"Build Status\"></a></h1>\n<blockquote>\n<p>Convert a dash/dot/underscore/space separated string to camelCase: <code>foo-bar</code> → <code>fooBar</code></p>\n</blockquote>\n<h2>Install</h2>\n<pre><code>$ npm install --save camelcase\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">const camelCase = require('camelcase');\n\ncamelCase('foo-bar');\n//=> 'fooBar'\n\ncamelCase('foo_bar');\n//=> 'fooBar'\n\ncamelCase('Foo-Bar');\n//=> 'fooBar'\n\ncamelCase('--foo.bar');\n//=> 'fooBar'\n\ncamelCase('__foo__bar__');\n//=> 'fooBar'\n\ncamelCase('foo bar');\n//=> 'fooBar'\n\nconsole.log(process.argv[3]);\n//=> '--foo-bar'\ncamelCase(process.argv[3]);\n//=> 'fooBar'\n\ncamelCase('foo', 'bar');\n//=> 'fooBar'\n\ncamelCase('__foo__', '--bar');\n//=> 'fooBar'\n</code></pre>\n<h2>Related</h2>\n<ul>\n<li><a href=\"https://github.com/sindresorhus/decamelize\">decamelize</a> - The inverse of this module</li>\n<li><a href=\"https://github.com/SamVerschueren/uppercamelcase\">uppercamelcase</a> - Like this module, but to PascalCase instead of camelCase</li>\n</ul>\n<h2>License</h2>\n<p>MIT © <a href=\"https://sindresorhus.com\">Sindre Sorhus</a></p>","docPath":"argo-ci/node_modules/camelcase/readme","proj":"argo-ci"}}

